<script type="text/babel">

import Recept from '../components/recept.vue';
import { Carousel, Slide } from 'vue-carousel';

export default {
  name: 'landing',
  components: {
    recept: Recept,
    carousel: Carousel,
    slide: Slide
  },
  props: ['base_url'],
  data () {
    return {
      carousel: [],
      recepti:      [],
      recepti_page: 1,
      recepti_has_more: true,
      fb: {
        shareUrl: 'https://www.facebook.com/sharer/sharer.php?u=' + this.base_url
      }
    }
  },
  mounted() {
    this.appendData();

//    (function(d, s, id) {
//      var js, fjs = d.getElementsByTagName(s)[0];
//      if (d.getElementById(id)) return;
//      js = d.createElement(s); js.id = id;
//      js.src = "//connect.facebook.net/sl_SI/sdk.js#xfbml=1&version=v2.9&appId=523407941381727";
//      fjs.parentNode.insertBefore(js, fjs);
//    }(document, 'script', 'facebook-jssdk'));

    (function(d, s, id) {
      var js, fjs = d.getElementsByTagName(s)[0];
      if (d.getElementById(id)) return;
      js = d.createElement(s); js.id = id;
      js.src = "//connect.facebook.net/sl_SI/sdk.js#xfbml=1&version=v2.9&appId=523407941381727";
      fjs.parentNode.insertBefore(js, fjs);
    }(document, 'script', 'facebook-jssdk'));

    this.carousel  = JSON.parse(window['anton_slike']);
    console.log(this.carousel)
  },

  methods: {
    share(e) {

      let shareUrl = 'https://www.facebook.com/sharer/sharer.php?u=' +
              encodeURIComponent(this.base_url, this.$route.path).replace(/%20/g,'+')
              + '&description=' + encodeURIComponent('Mesu primešamo nasesekljano čebulo, solimo, popramo. Oblikujemo poljubne oblike. Popečemo na razgreti ponvi. Bombetke popečemo jih prerežemo, pomažemo z omakami in nadevamo z mesom in zelenjavo.').replace(/%20/g,'+')
              + '&title=' + encodeURIComponent('Burger Anton').replace(/%20/g,'+')
              + '&picture=' + encodeURIComponent(this.base_url + '/wp-content/themes/mesaranton/dist/img/burger.png').replace(/%20/g,'+')


      window.open(shareUrl, '_blank', 'width=520,height=350')

      e.preventDefault();

    },
    appendData() {
      this.$http.get(this.base_url + 'wp-json/wp/v2/recepti', {
        params: {
          per_page: 6,
          page: this.recepti_page
        }
      }).then(response => {
        if (response.status == 200) {
          if (this.recepti_page >= parseInt(response.headers.map['X-WP-TotalPages'])) {
            this.recepti_has_more = false;
          }

          for (var i = 0; i < response.data.length; i++) {
            this.recepti.push(response.data[i]);
          }
        }
      }, response => {
        // error callback
      });
    },

    loadMore(e) {
      this.recepti_page++;
      this.appendData();
    }
  }

}
</script>


