<template>
    <div class="col-md-4">
        <router-link :to="link" class="recipe">
            <div class="recipes__image" :style="{ 'background-image': 'url(' + bgImage + ')' }"></div>
            <div class="recipes__text">
                <h4 class="recipes__heading">{{ recept.title.rendered }}</h4>
                <div class="recipes__author">{{ recept.acf.avtor }}</div>
            </div>
            <div class="recipes__social"><span>
                <iframe :src="getLikeIframeSrc" frameborder="0"></iframe>
            </span></div>
        </router-link>
    </div>
</template>


<script type="text/babel">
    import fb from '../mixins/fb';

    export default {
        name: 'recept',
        mixins: [fb],
        props: ['data'],
        data() {
          return {
              bgImage: '',
              recept: {
                  title: {
                      rendered: ''
                  },
                  acf: {
                      avtor: ''
                  }
              }
          }
        },
        computed: {
            link() {
                return '/recepti/'+this.data.slug;
            },
            fullLink() {
                return document.getElementsByTagName('base')[0].href + 'recepti/'+ this.data.slug
            },
            getLikeIframeSrc() {
                if (this.recept != null) {
                    return 'https://www.facebook.com/v2.9/plugins/like.php?action=like&app_id=523407941381727&domain='+document.getElementsByTagName('base')[0].href.replace('http://')+'&origin='+this.base_url+'&relation=parent.parent&container_width=0&href='+document.getElementsByTagName('base')[0].href + 'recepti/'+ this.recept.slug+'&layout=button_count&locale=sl_SI&sdk=joey&send=true&share=false&show_faces=false&size=large';

                } else {
                    return '';
                }
            }
        },
        mounted() {
            this.recept = this.data;

            if (typeof this.data != 'undefined') {
                if (typeof this.data.acf.slika != 'undefined' && this.data.acf.slika) {
                    this.bgImage = this.data.acf.slika.sizes.medium_large;
                }
            }

//            this.getLikeCount(document.getElementsByTagName('base')[0].href + 'recepti/'+ this.data.slug);
        }


    }
</script>


