import Vue from 'vue'
import App from './App.vue'
import VueRouter from 'vue-router'
Vue.use(VueRouter);

import Meta from 'vue-meta'
Vue.use(Meta);

import VueResource from 'vue-resource';
Vue.use(VueResource);


var Landing = require('./pages/landing.vue');
var Add = require('./pages/add.vue');
var Single = require('./pages/single.vue');
var Thankyou = require('./pages/thankyou.vue');
var Legal = require('./pages/legal.vue');


var router = new VueRouter({
  mode: 'history',
  base: '/',
  routes: [
    { path: '/', component: Landing},
    { path: '/dodaj', component: Add},
    { path: '/thankyou', component: Thankyou},
    { path: '/recepti/:slug', component: Single},
    { path: '/zapisnik', component: Legal},
    { path: '/pravila-sodelovanja', component: Legal},
  ]
});

new Vue({
  el: '#app',
  router: router,
  render: h => h(App)
})



