<template>
    <div id="legal">


        <header class="single" style="background-image: url(/wp-content/themes/mesaranton/dist/img/add_bg.png);">
            <div class="container">
                <div class="row">
                    <div class="col-sm-12">
                        <router-link to="/" class="hero__logo"><img src="/wp-content/themes/mesaranton/dist/img/logo.png" /></router-link>
                    </div>
                </div>
            </div>
        </header>

        <div class="single__content">
            <div class="container">
                <div class="row">
                    <div class="col-md-8 offset-md-2 col-sm-12 single__back">
                        <a href="#" v-on:click="goBack"  class="single__back-link"></a>
                        <h1>{{ page.title.rendered }}</h1>
                        <hr>
                        <br>

                        <div v-html="page.content.rendered"></div>
                        <br>
                        <br>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script type="text/babel">

    export default {
        name: 'legal',
        props: ['base_url'],
        data () {
            return {
                page: null
            }
        },
        mounted() {
            this.$http.get(this.base_url + 'wp-json/wp/v2/pages', {
                params: {
                    slug: this.$route.path.replace('/', '')
                }
            }).then(response => {
                if (response.status == 200 && response.data.length) {
                    this.page = response.data[0];
                }
            }, response => {
                    // error callback
            });
            window.scrollTo( 0, 0 );
        },
        methods: {
            goBack(e) {
                e.preventDefault()
                this.$router.go(-1);
            }
        }
    }
</script>


