<template>
    <div id="add">

        <div class="modal_share__wrapper" v-if="showModal">
            <div class="modal_share">
                <a v-on:click="closeModal" href="#" class="close_modal"></a>
                <h2>{{ terms.title }}</h2>
                <hr>
                <div class="modal_scrollable" v-html="terms.content"></div>

                <div class="clearfix">
                    <a v-on:click="closeModal" href="#" class="btn btn-grey">Zapri</a>
                </div>
            </div>
        </div>


        <header class="add" style="background-image: url(/wp-content/themes/mesaranton/dist/img/add_bg.jpg);">
            <div class="container">
                <div class="row">
                    <div class="col-sm-12">
                        <router-link to="/" class="hero__logo"><img src="/wp-content/themes/mesaranton/dist/img/logo.png" /></router-link>
                    </div>
                </div>
            </div>
        </header>

        <div class="add__content">
            <div class="container">
                <div class="row">
                    <div class="col-md-8 offset-md-2 col-sm-12 single__back">
                        <router-link to="/" class="single__back-link"></router-link>
                        <h1>Sodeluj in ustvari svoj recept</h1>

                        <p>V Antonovi okusni nagradni igri sodeluješ čisto enostavno. Spodaj po korakih vpiši želene podatke - od naslova recepta pa vse do edinstvenega načina priprave, ki ga želiš deliti z drugimi. Ne pozabi na sestavine!</p>
                        <p>Nato podeli svoj recept na Facebook omrežju - zmagal bo namreč recept z največ všečki. Držimo pesti, da bo tvoj naslednji piknik še boljši - z Antonom!</p>

                        <hr>

                        <div v-if="errors.length" class="alert alert-danger" role="alert">
                            <p v-for="e in errors">{{ e }}</p>

                        </div>


                        <form id="addForm" v-on:submit.prevent="onSubmit" method="POST" enctype="multipart/form-data" accept="image/*">
                            <div class="form-group">
                                <label>Naslov recepta</label>
                                <input type="text" name="post_title" :class="{ 'form-control-danger': hasError('post_title'), 'form-control': true }" />
                            </div>
                            <div class="form-group">
                                <label>Vaše ime in priimek</label>
                                <input type="text" name="field[avtor]" :class="{ 'form-control-danger': hasError('avtor'), 'form-control': true }" />
                            </div>
                            <div class="form-group">
                                <label>Vaš elektronski naslov</label>
                                <input type="email" name="field[avtor_email]" :class="{ 'form-control-danger': hasError('avtor_email'), 'form-control': true }" />
                            </div>

                            <div class="form-group">

                            <label>Naloži fotografijo</label>
                            <label class="file-upload">
                                <input v-on:change="onFileSelect" name="field[slika]" type="file" id="file" :class="{ 'form-control': true, 'file-upload__input': true }">
                                <span :class="{ 'form-control-danger': hasError('slika'), 'form-control': true, 'file-upload__control': true }"></span>
                            </label>
                            </div>

                            <div class="form-group">
                                <label>Sestavine</label>
                                <textarea :class="{ 'form-control-danger': hasError('sestavine'), 'form-control': true }" name="field[sestavine]" rows="6"></textarea>
                            </div>
                            <div class="form-group">
                                <label>Opis priprave jedi</label>
                                <textarea :class="{ 'form-control-danger': hasError('priprava'), 'form-control': true }" name="field[priprava]" rows="10"></textarea>
                            </div>

                            <div class="row add__submit-wrapper">
                                <div class="col-sm-6 col-xs-12">
                                    <label class="form-check-label">
                                        <div v-on:click="onTermsClick" :class="{ 'add__pogoji': true ,'form-checked': termsChecked, 'form-danger': termsAccepted}">
                                            <label>
                                                <input v-on:change="onTerms" type="radio" name="pogoji" class="add__pogoji_radio">
                                                Strinjam se s <a href="#" v-on:click="openModal">pogoji sodelovanja</a>

                                            </label>
                                        </div>
                                    </label>
                                </div>
                                <div class="col-sm-6  col-xs-12 text-right"><button  class="btn btn-primary">Sodeluj in oddaj svoj recept!</button></div>
                            </div>
                        </form>

                    </div>
                </div>
            </div>
        </div>

        <div class="recipes">
            <div class="container">
                <div class="row">
                    <div class="col-md-12"><h2>Recepti naših oboževalcev!</h2></div>
                    <recept v-for="r in recepti" :data="r" :key="r.id"></recept>
                </div>
            </div>
        </div>

    </div>
</template>

<script type="text/babel">
    import latest from '../mixins/latest';
    import Recept from '../components/recept.vue';
    export default {
        name: 'add',
        mixins: [latest],
        props: ['base_url'],
        components: {
            recept: Recept
        },
        data () {
            return {
                errors: [],
                file: null,
                pogoji: null,
                termsAccepted: false,
                termsChecked: false,
                showModal: false,
                terms: {
                    title: '',
                    content: ''
                }
            }
        },
        mounted() {
            window.scrollTo( 0, 0 );
            this.getTermsModal();
        },
        methods: {
            openModal(e) {
                e.preventDefault();
                this.showModal = true;
            },
            closeModal(e) {
                e.preventDefault();
                this.showModal = false;
            },
            getTermsModal() {
                this.$http.get(this.base_url + 'wp-json/wp/v2/pages/90').then(response => {
                    if (response.status == 200) {
                        this.terms.title = response.data.title.rendered;
                        this.terms.content = response.data.content.rendered;
                    }
                }, response => {
                    // error callback
                });
                window.scrollTo( 0, 0 );

            },
            onTermsClick(e) {
                this.termsChecked = e.target.value == 'on'
            },
            termsNotAccepted() {
                if (this.pogoji == null) {
                    this.termsAccepted = true;
                    return true;
                } else if (this.pogoji == ""){
                    return true;
                } else {
                    return this.pogoji;
                }
            },
            onTerms() {
                if (this.pogoji == null) {
                    this.pogoji = true;
                } else {
                    this.pogoji = !this.pogoji
                }
            },
            hasError(key) {

                return typeof this.errors[key] != 'undefined'
            },
            onFileSelect(e) {
                let file = e.target.files || [];
                if (!file.length)
                    return;

                document.styleSheets[0].addRule('.file-upload__control::before','content: "'+file[0].name+'" !important;');
                this.createImage(file[0]);
            },
            createImage(file) {
                let reader = new FileReader();
                let vm = this;
                reader.onload = (e) => {
                    vm.image = e.target.result;
                };
                reader.readAsDataURL(file)
                this.file = file;
            },
            onSubmit() {
                let form = document.getElementById('addForm');
                let formData = new FormData(form);
                formData.append('file', this.file);
//                formData.append('foo', 'bar');
                this.$http.post(this.base_url + 'wp-json/mesaranton/v1/recept', formData, {
                    emulateJSON: true
                }).then(response => {
                    if (response.status == 200) {


                        ga('send', 'event', 'CTA', 'click', 'sodeluje');
                        fbq('track', 'CompleteRegistration');

                        this.$router.push({ path: '/recepti/'+response.data, query: { thankyou: true }});
                    }
                }, response => {
                    this.errors = response.data.data;
                    this.termsNotAccepted()
                });
            }
        }
    }
</script>


